
/* eslint no-unused-vars: 0 */
import ifDefined from '@ui5/webcomponents-base/dist/renderer/ifDefined.js';
import { html, svg, repeat, classMap, styleMap } from '@ui5/webcomponents-base/dist/renderer/LitRenderer.js';
const block0 = (context) => { return html`<div class="ui5-uc-root"><div class="ui5-uc-header"><slot name="header"></slot></div><div class="ui5-uc-content"><ui5-list			mode="${ifDefined(context.mode)}"			@ui5-selectionChange="${ifDefined(context._onSelectionChange)}"			@ui5-itemDelete="${ifDefined(context._onItemDelete)}"		><slot></slot></ui5-list>		${ context._showNoData ? block1(context) : block2(context) }</div></div>`; };
const block1 = (context) => { return html`<div class="uc-no-files"><div class="icon-container"><ui5-icon name="document"></ui5-icon></div><ui5-title level="H2">${ifDefined(context._noDataText)}</ui5-title><ui5-label class="subtitle">${ifDefined(context._noDataDescription)}</ui5-label></div>		`; };
const block2 = (context) => { return html`${ context._showDndOverlay ? block3(context) : undefined }`; };
const block3 = (context) => { return html`<div				class="${classMap(context.classes.dndOverlay)}"				@dragenter="${context._ondragenter}"				@dragleave="${context._ondragleave}"				@dragover="${context._ondragover}"				@drop="${context._ondrop}"			><ui5-icon name="upload-to-cloud"></ui5-icon><span class="dnd-overlay-text">${ifDefined(context._dndOverlayText)}</span></div>		`; };

export default block0;